* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Medium.91322885.ttf");
}

body {
  background-color: gray;
  justify-items: center;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  display: flex;
}

header {
  text-align: center;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo img {
  width: 40px;
  height: 40px;
}

.nav_list {
  text-align: center;
  height: 60px;
  list-style: none;
  display: flex;
}

.nav_item {
  position: relative;
}

nav ul li a {
  color: #000;
  margin: 0 10px;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

nav ul li a:hover, nav ul li a.is-active {
  color: #00acdc;
}

nav ul li a.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

nav ul li a.hover {
  color: #00acdc;
}

nav ul li:first-child a {
  margin-left: 0;
}

nav ul li:last-child a {
  margin-right: 0;
}
